import { useEffect, useState } from "react"

export const useActiveHash = (itemIds, rootMargin = undefined) => {
  const [activeHash, setActiveHash] = useState(``)
  // activeHash = , setActiveHash = activeHash   set  , useState 
  useEffect(() => {
  // useEffect             Hook 
  //   componentDidMount  componentDidUpdate     .
    const observer = new IntersectionObserver(
      entries => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            //      !
            setActiveHash(entry.target.id)
          }
        })
      },
      { rootMargin: rootMargin || `0% 0% -80% 0%` }
      // root    viewport . bottom -80%    Tag   isIntersecting 
      // -80%   Tag    isIntersecting  !
    )

    itemIds.forEach(id => { 
      //    !  setActiveHash(entry.target.id)   bold  
      observer.observe(document.getElementById(id))
    }) 


    return () => {
      itemIds.forEach(id => {
        // observer.observe(document.getElementById(id))   clean up 
        observer.unobserve(document.getElementById(id))
      })
    }
  }, [itemIds,rootMargin])

  return activeHash
}